class GlobalConfigUtil {
  getField (globalConfig, serviceName, field) {
    const entity = getComponent(globalConfig, serviceName).entity
    return entity.find((item) => item.field === field)
  }

  getComponent (globalConfig, serviceName) {
    const services = getServices(globalConfig)
    const service = services.find((service) => {
      return service.name === serviceName
    })
    return service
  }

  getServices (globalConfig) {
    return globalConfig.pages.inputs.services
  }
}
export const { getField, getComponent, getServices } = new GlobalConfigUtil()
